import * as React from "react"
import { Flex, Text, VStack, useMediaQuery, Box, Icon } from "@chakra-ui/react"
import { FaReact, FaLinux, FaPython } from "react-icons/fa"
import { SiC, SiCplusplus } from "react-icons/si";
import { StaticImage } from "gatsby-plugin-image"



const AppAbout = () => {
    const [isLargerThan480] = useMediaQuery('(min-width: 600px)')
    return (
        <>
            <Flex
                //border={"1px"} borderColor='blue'
                //pos='fixed'
                //boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
                borderRadius="15px"
                w="100%"
                h='100%'
                flexDir='column'
            //justifyContent="space-between" 
            >

                <Flex
                    border={"1px"} borderColor='yellow'
                    //pos='fixed'
                    borderRadius="15px"
                    w="100%"
                    h='100vh'
                    flexDir={(isLargerThan480 ? "row" : "column")}
                //py={(isLargerThan480 ? "0" : "35")}
                //pl={(isLargerThan480 ? "0" : "10%")}
                //justifyContent="space-between" 
                //alignItems="flex-start"

                >
                    <Flex
                        border={"1px"} borderColor='blue'
                        //pos='fixed'
                        borderRadius="30px"
                        //w={isLargerThan480 ? '100%' : '100vw'}
                        h={isLargerThan480 ? '60%' : '100%'}
                        flexDir={(isLargerThan480 ? "row" : "column")}
                        mt={(isLargerThan480 ? "50px" : "10")}
                        p="3"
                        //mb={(isLargerThan480 ? "200px" : "0")}
                        ml={[0, 150]}
                        //mr={[0, 10]}
                        //justifyContent="space-evenly"
                        alignItems={(isLargerThan480 ? "flex-start" : "center")}
                    >
                        <Flex
                            flexDir="column"
                            w="100%" h="100%"
                        >
                            <Box
                                border={"1px"}
                             align={(isLargerThan480 ? "flex-start" : "center")}
                            >
                                <Text fontSize="md" fontWeight="semibold" >
                                    I studied at Universidad Autonoma de Baja California, I
                                    am a person with abilities working in groups, adaptable
                                    to different roles and with a sense of responsability. I
                                    am looking to apply my knowledges and gain laboral experience, some of my main interests are circuits design,
                                    web programming and embedded systems.
                                </Text>
                            </Box>

                        </Flex>

                        <Box
                            border={"1px"}
                            ml={(isLargerThan480 ? "5" : "0")}
                        >
                            <StaticImage
                                style={{ borderRadius: "80vh" }}
                                src="../images/profile.jpg"
                                layout="constrained"
                                alt="A dinosaur"
                                placeholder="blurred"
                            />
                        </Box>
                    </Flex>
                </Flex>
            </Flex>
        </>
    )
}

export default AppAbout
