import * as React from "react"
import {
    Flex
    , Text
    , Icon
    , Link
    , Menu
    , MenuButton
    , MenuList
} from "@chakra-ui/react"
import AppNavHoverBox from "./AppNavHoverBox"

const AppNavItem = ({ isDark, isLargerThan480, title, icon, active, description }) => {
    return (
        <Flex
            //display={isSidebarMinimized ? "none" : "flex"}
            bgGradient={isDark ? "linear(to-r, orange.200 0%, yellow.100 50%, red.100 95%)": "linear(to-r, #1A202C,#9F7AEA)"}
            color={isDark ? "black" : "White"}
            borderRadius="15px"
            mt='27'
            flexDir='column'
            w='100%'
            alignItems='flex-start'
        >
            <Menu  borderRadius='15px' placement="right">
                <Link
                    backgroundColor={active && '#FFFFFF'}
                    p={3}
                    borderRadius='15px'
                    _hover={{ textDecor: "none", backgroundColor: "purple.400" }}
                    w="100%"
                >
                    <MenuButton w='100%'>
                        <Flex>
                            <Icon as={icon} fontSize='xl'  />
                            <Text fontWeight='semibold' ml={5} display={isLargerThan480 ? 'flex-start' : 'none'}>{title}</Text>
                        </Flex>
                    </MenuButton>
                </Link>
                <MenuList
                    py={0}
                    border='none'
                    w={200}
                    h={200}
                    ml={5}
                >
                    <AppNavHoverBox title={title} icon={icon} description={description} />
                </MenuList>
            </Menu>
        </Flex>
    )
}

export default AppNavItem
