import React from 'react'
import { Flex, useColorMode,useMediaQuery } from "@chakra-ui/react"


import AppSidebar from "../components/AppSidebar"
import AppHome from "./AppHome"
import AppAbout from "./AppAbout"
import AppSoftware from './AppSoftware'
import AppHardware from './AppHardware'
import AppProjects from './AppProjects'

const AppLayout = () => {
    const { colorMode, toggleColorMode } = useColorMode();
    const isDark = colorMode === "dark";
    const [isLargerThan480] = useMediaQuery('(min-width: 480px)')
    
    return (
        <Flex
            //border={"1px"} borderColor='blue'
            borderRadius="30px"
            w="100%"
            h='100%'
            flexDir='column'
        > 
            <AppSidebar isDark={isDark} isLargerThan480={isLargerThan480} />
            <AppHome isDark={isDark} isLargerThan480={isLargerThan480}/>
            <AppAbout />
            <AppHardware />
            <AppSoftware />
            <AppProjects />
            

        </Flex>
    )
}

export default AppLayout
