import * as React from "react"
import { Flex, Text, Stack, VStack, Box, Icon, Spacer } from "@chakra-ui/react"
import { FaReact, FaLinux, FaPython, FaCubes } from "react-icons/fa"
import { GiGuardedTower, GiHeartTower } from 'react-icons/gi'
import { SiC, SiCplusplus } from "react-icons/si";
import { StaticImage } from "gatsby-plugin-image"



const AppHome = ({ isDark, isLargerThan480 }) => {
    return (
        <>
            <Flex
                border={"1px"} borderColor='red'
                bgGradient={isDark ? "linear(to-b, #000000,#322659,#9F7AEA)" : "linear(to-b, orange.200 0%, yellow.100 50%, red.100 95%)"}
                borderRadius="15px"
                w="100%"
                h='100vh'
                flexDir={(isLargerThan480 ? "row" : "column")}
            >
                <Flex
                    flexDir="column"
                    w="100%" h="100%"
                >
                    <Flex
                        style={{ display: "grid" }}
                        border={"1px"} borderColor='blue'
                        borderRadius="30px"
                        //w={isLargerThan480 ? '80vw' : '100%'}
                        h={isLargerThan480 ? '60%' : '80vh'}
                        flexDir={(isLargerThan480 ? "row" : "column")}
                        mt={(isLargerThan480 ? "50px" : "10")}
                        ml={[0, 150]}
                        justifyContent="space-evenly"
                    >
                        <Flex
                            style={{ gridArea: "1/1" }}
                            pt={[100, 50]}
                            //pl={[0, 150]}
                            justifyContent="space-evenly"
                        >
                            <Icon borderRadius="5px" w={200} h={200} as={FaCubes} p={0.5} />
                            <Icon borderRadius="5px" w={200} h={200} as={FaCubes} p={0.5} display={isLargerThan480 ? "" : "none"} />
                            {/* <Icon borderRadius="5px" w={150} h={150} as={GiHeartTower} p={0.5} /> */}
                        </Flex>

                        <Box
                            style={{ gridArea: "1/1" }}
                            //border={"1px"}
                            align="center"
                        >
                            <Text fontSize="4xl" fontWeight="semibold" >I'm</Text>
                            <Text
                                fontSize={['5xl', '8xl', '8g', '9xl', '8xl']}
                                fontWeight="bold"
                                bgGradient="linear(to-r,cyan.400,blue.500,purple.600)"
                                bgClip="text"
                                mt="10">David Torres
                            </Text>
                            <Text fontSize="md" fontWeight="semibold" >Electronics Engineer and Web Developer</Text>
                        </Box>
                    </Flex>

                    <Flex
                        border={"1px"} borderColor='blue'
                        borderRadius="30px"
                        //w={isLargerThan480 ? '80vw' : '100%'}
                        h={isLargerThan480 ? '40%' : '20%'}
                        flexDir="row"
                        p="3"
                        ml={[0, 150]}
                        //justifyContent="space-evenly"
                        alignItems="flex-start"
                    >

                        <VStack spacing="0">
                            <Icon bg="gray.400" borderRadius="5px" w={10} h={10} as={FaLinux} color="Black" p={0.5} />
                            <Text fontSize="sm" fontWeight="semibold" >Linux</Text>
                        </VStack>

                        <VStack pl="10px" spacing="0">
                            <Icon bg="gray.400" borderRadius="5px" w={10} h={10} as={SiC} color="Black" p={0.5} />
                            <Text fontSize="sm" fontWeight="semibold" >C</Text>
                        </VStack>

                        <VStack pl="10px" spacing="0">
                            <Icon bg="gray.400" borderRadius="5px" w={10} h={10} as={SiCplusplus} color="Black" p={0.5} />
                            <Text fontSize="sm" fontWeight="semibold" >C++</Text>
                        </VStack>

                        <VStack pl="10px" spacing="0">
                            <Icon bg="gray.400" borderRadius="5px" w={10} h={10} as={FaPython} color="Black" p={0.5} />
                            <Text fontSize="sm" fontWeight="semibold" >Python</Text>
                        </VStack>

                        <VStack pl="10px" spacing="0">
                            <Icon bg="gray.400" borderRadius="5px" w={10} h={10} as={FaReact} color="Black" p={0.5} />
                            <Text fontSize="sm" fontWeight="semibold" >React</Text>
                        </VStack>

                    </Flex>
                </Flex>
            </Flex>
        </>
    )
}

export default AppHome
