import * as React from "react"
import { Flex, IconButton, useColorMode} from "@chakra-ui/react"
import { GoCircuitBoard, GoBriefcase, GoPerson } from "react-icons/go";
import { FaMoon, FaSun, FaLinkedin, FaGithub, FaFileExport, FaGlobeAmericas, FaDev, FaBars } from "react-icons/fa"
import AppNavItem from "./AppNavItem"
import { useDisclosure, Lorem, Collapse } from '@chakra-ui/react'

const AppSidebar = ({ isDark, isLargerThan480 }) => {
    const { colorMode, toggleColorMode } = useColorMode();
    const [isSidebarMinimized, scrollSidebar] = React.useState(isLargerThan480 ? false : true)
    const { isOpen, onToggle } = useDisclosure()
    
    return (
        <>
            <Flex
                //bgColor='pink.200' header
                pos='fixed'
                h='30px'
                marginTop='0.5vh'
                boxShadow='0 4px 12 px 0 rgba(0, 0, 0,  0.05)'
                borderRadius="15px"
                w="100%"
                flexDir='row'
                justifyContent="space-between"
                zIndex="docked"
                >
                
                <Flex
                    flexDir="column"
                    p="1"
                    w="100%"
                    align="flex-end">
                    <Flex align="center">

                        <a href="https://www.linkedin.com/in/damzts/" target="_blank" rel="noopener noreferrer">
                            <IconButton
                                background="none"
                                borderRadius="10px"
                                ml={1}
                                size="sm" icon={isDark ? <FaLinkedin color="White" /> : <FaLinkedin color="Black" />}
                                _hover={{ background: 'gray.600' }}
                            />
                        </a>
                        <a href="https://github.com/damzts" target="_blank" rel="noopener noreferrer">
                            <IconButton
                                borderRadius="10px"
                                ml={1}
                                size="sm" icon={isDark ? <FaGithub color="White" /> : <FaGithub color="Black" />}
                                _hover={{ background: 'gray.600' }}
                                background="none"
                            />
                        </a>
                        <IconButton
                            borderRadius="10px"
                            ml={1}
                            size="sm" icon={isDark ? <FaSun color="White" /> : <FaMoon color="Black" />}
                            _hover={{ background: 'gray.600' }}
                            bgColor={isDark ? "Black" : "White"}
                            onClick={toggleColorMode}
                        />
                    </Flex>
                </Flex>
            </Flex>


            <Flex
                //bgColor='gray.500' sidebar
                pos='fixed'
                left='1'
                h='96vh'
                marginTop={(isLargerThan480 ? '0' : '5vh')}
                boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
                borderRadius={(isLargerThan480 ? '30px' : '15px')}
                //w={[50, 180]}
                flexDir='column'
                justifyContent="space-between"
            >
                <Flex
                    p='5%'
                    flexDir='column'
                    alignItems='flex-start'
                    as='nav'
                >
                    <IconButton
                        alignSelf='flex-start'
                        mt={3}
                        _hover={{ background: "purple.400" }}
                        icon={<FaBars color={isDark?"Black":"White"} />}
                        bgGradient={isDark ? "linear(to-b, orange.200 0%, yellow.100 50%, red.100 95%)":"linear(to-b, #1A202C,#9F7AEA)"}
                        onClick={() => {
                            if (isSidebarMinimized)
                                scrollSidebar(true)
                            else
                                scrollSidebar()
                            onToggle()
                        }}

                    />
                    <Collapse in={isLargerThan480 ? isSidebarMinimized ? !(isOpen) : isOpen : isOpen} >
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='Home' icon={GoPerson} description='This is the description for the About Hover' />
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='About' icon={FaGlobeAmericas} />
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='Hardware' icon={GoCircuitBoard} />
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='Software' icon={FaDev} />
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='Projects' icon={GoBriefcase} />
                        <AppNavItem isDark={isDark} isLargerThan480={isLargerThan480} title='Resume' icon={FaFileExport} />
                    </Collapse>
                </Flex>

                {/* <Flex
                    p='5%'
                    flexDir='column'
                    w='100%'
                    align={isLargerThan480 ? 'flex-start' : 'center'}
                    mb={4}
                >
                    <Divider display={isLargerThan480 ? 'flex-end' : 'none'}
                    />
                    <Flex mt={3} align='center'>
                        <Avatar size='sm' src={profile} />
                        <Flex flexDir='column' ml={4} display={isLargerThan480 ? 'flex-end' : 'none'}>
                            <Heading as='h3' size='sm'>David Torres</Heading>
                            <Text>Admin</Text>
                        </Flex>
                    </Flex>
                </Flex> */}
            </Flex>
        </>
    )
}

export default AppSidebar
