import * as React from "react"
import { Flex, Text, VStack, useMediaQuery, Box, Icon } from "@chakra-ui/react"
import { FaReact, FaLinux, FaPython } from "react-icons/fa"
import { SiC, SiCplusplus } from "react-icons/si";
import { StaticImage } from "gatsby-plugin-image"



const AppHardware = () => {
    const [isLargerThan480] = useMediaQuery('(min-width: 600px)')
    return (
        <>
            <Flex
                //border={"1px"} borderColor='blue'
                //pos='fixed'
                //boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
                borderRadius="15px"
                w="100%"
                h='100%'
                flexDir='column'
            //justifyContent="space-between" 
            >

                <Flex
                    border={"1px"} borderColor='yellow'
                    //pos='fixed'
                    borderRadius="15px"
                    w="100%"
                    h='100vh'
                    flexDir={(isLargerThan480 ? "row" : "column")}
                //py={(isLargerThan480 ? "0" : "35")}
                //pl={(isLargerThan480 ? "0" : "10%")}
                //justifyContent="space-between" 
                //alignItems="flex-start"

                >
                    <Flex
                        border={"1px"} borderColor='blue'
                        //pos='fixed'
                        borderRadius="30px"
                        //w={isLargerThan480 ? '100%' : '100vw'}
                        h={isLargerThan480 ? '60%' : '100%'}
                        flexDir={(isLargerThan480 ? "row" : "column")}
                        mt={(isLargerThan480 ? "50px" : "10")}
                        p="3"
                        //mb={(isLargerThan480 ? "200px" : "0")}
                        ml={[0, 150]}
                        //mr={[0, 10]}
                        //justifyContent="space-evenly"
                        alignItems={(isLargerThan480 ? "flex-start" : "center")}
                    >
                        <Flex
                            flexDir="column"
                            w="100%" h="100%"
                        >
                            <Box
                                border={"1px"}
                             align={(isLargerThan480 ? "flex-start" : "center")}
                            >
                                <Text fontSize="md" fontWeight="semibold" >
                                    Hello from AppHardware
                                </Text>
                            </Box>
                        </Flex>
                    </Flex>
                </Flex>
            </Flex>
        </>
    )
}

export default AppHardware
